/*********============== GLOBAL STYLES ==============*********/

/*** Box Sizing ***/
html {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  font-size: 100%;
  height: 100%;
}
*, *:before, *:after {
  -moz-box-sizing: inherit;
       box-sizing: inherit;
}

/******================ FONTS & MARGINS =================******/
body {
	background-color: #ffffff;
  font-family: 'Noto Sans', 'Avenir Next', 'SegoeUI', 'Arial', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  height: 100%;
}

/*** Paragraph ***/
p {
  font-size: 1em;
  max-width: 36em;
  color: #3b4348;
	margin: 0;
  -webkit-hyphens: auto;
     -moz-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

* + p {
   margin-top: 1.35rem;
}

/*** Heading ***/
h1, h2, h3, h4, h5, h6 {
	font-family: 'Droid Serif', 'Georgia Pro', 'Georgia', 'Times', 'Times New Roman', serif;
	margin: 0;
  margin-top: 24px;          /* non-scalable fallback for old browsers */
  margin-top: 1.5rem;        /* 24px, will be ignored by old browsers */
}
h1 {
	font-size: 3.297em;
}
h2 {
	font-size: 2.703em;
}
h3 {
	font-size: 2.215em;
}
h4 {
	font-size: 1.816em;
}
h5 {
	font-size: 1.488em;
}
h6 {
	font-size: 1.22em;
}

/*** Link Styles ***/
a {
	text-decoration: none;
}
a:link {
  color: #ff6732;
}
a:visited {
  color: #ff6732;
}
a:hover,
a:focus {
color: #94f39f;
}
a:active {
  color: #ff9d00;
}

/***** Images *****/
img {
	width: auto;
  max-width: 100%;   /* max-width allows image to be responsive. */
  height: auto;
}

/*** Reset margin & padding to 0. ***/
header, main, section, footer {
	margin: 0;
  margin-top: 24px;
  margin-top: 1.5rem;
	padding: 0;
}

/***** Mobile first styles go here *****/

/**** Wrapper Div ****/
.wrap {
  width: 95%;
  max-width: 71.25em;
  margin: 0 auto;
}

/*********====== HEADER ======**********/

header h1 {
	padding: 0;
}

header h1 a {
	display: block;
	width: 100%;
  text-align: center;
}
header h1 a:link {
	color: #000;
}
header h1 a:visited {
	color: #000;
}
header h1 a:hover, a:focus {
	color: #3b423c;
}
header h1 a:active {
	color: #0f1314;
}

/******===== HEADER - Navigation =====******/
nav {
  margin-top: 1rem;
 	border: 1px solid #c8bc9d;
 }

nav a {
	display: inline-block;
	font-size: 1.1em;
 	width: 50%;
 	text-transform: uppercase;
  text-align: center;
 	padding: 1em 0;
  margin: 0;
 	letter-spacing: 0.12em;
 }
nav a:link {
  color: #ff6732;
}
nav a:visited {
  color: #ff6732;
}
nav a:hover, a:focus {
	background-color:  #f9d475;
	color: #000000;
}

/**********============ SECTION - about ===========***********/
.avatar, .bio {
	margin: 0;
  margin-top: 24px;
  margin-top: 1.5rem;
	padding: 0;
	width: 100%;
}
.avatar div {
	width: 130px;
	height: 130px;
	margin: 0 auto;
}

.bio > p {
  max-width: 36em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.bio > * + * {
  margin-top: 1.1rem;
}

.highlight {
	color: blueviolet;
  border-bottom: 1px solid blueviolet;
}
/**********============ SECTION - social ===========***********/
.social {
  display: none;
}

.social-m {
	margin: 1.5rem 0 0;
  padding: 1em 0;
	width: 100%;
  border: 1px solid #c8bc9d;
}
.social-m a {
  display: inline-block;
	margin: 0;
	padding: .1em;
	width: 25%;
  text-align: center;
  border-right: 1px solid #c8bc9d;
}
.social-m a:last-child {
  border-right: none;
}
.social-m a:link {
	color: #ff6732;
}
.social-m a:visited {
	color: #ff6732;
}
.social-m a:hover,a:focus {
	color: #94f39f;
}

/************========= FOOTER ==========*************/

footer, .push {
  text-align: center;
}
footer .wrap > * + * {
  margin-top: 1.5rem;
}

/************** Desktop styles go here ***************/
/***** TAB ****/
@media screen and (min-width: 35em) {
body {
	width: 100%;
	font-size: 1.18em;
}
}

/***** DESKTOP ****/
@media screen and (min-width: 50em) {
body {
	width: 100%;
	font-size: 1.1em;
}
}
